import React, { useState, useRef, useEffect } from "react";
import "./Teammates.css";
import icons from "../../assets/image/icons.svg";
import person from "../../assets/image/other/person@2x.png";
import doc from "../../assets/image/other/doc.png";
import * as requests from '../../api/requests';
import * as userService from '../../api/users';
import * as messagingService from '../../api/messaging';

export default function Teammates({ name, lastname, list, id, avatar }) {
  const [tools, setTools] = useState(false);
  const [chatShow, setChatShow] = useState(false);
  const [activeUsers, setActiveUsers] = useState([]);
  const [chatClass, setChatClass] = useState("open");

  const calcDistanceChat = (e) => {
    const distanceRight =
      window.innerWidth - e.currentTarget.getBoundingClientRect().right;
    const distanceLeft = e.currentTarget.getBoundingClientRect().left;
    if (distanceRight < 450) {
      setChatClass("open right");
    } else if (distanceLeft < 450 && distanceRight < 450) {
      setChatClass("open top");
    }
  };

  const ref = useRef(null);
  const chatRef = useRef(null);
  const chatRefList = useRef(null);

  useEffect(() => {
    

    changePos(chatRef.current);
    changePos(chatRefList.current);
    function handleClickOutside(event) {
      if (
        ref.current &&
        !ref.current.contains(event.target) &&
        !event.target.closest(".emoji-picker__wrapper")
      ) {
        setTools(false);
      }
    }
    document.addEventListener("mouseup", handleClickOutside);
    return () => {
      document.removeEventListener("mouseup", handleClickOutside);
    };
  }, [ref]);

  setTimeout(() => {
    
    let oldActiveUsers = JSON.stringify(activeUsers), newActiveUsers = JSON.stringify(messagingService.getActiveUsers());
    if(oldActiveUsers != newActiveUsers){
      setActiveUsers([...messagingService.getActiveUsers()]);
      console.log('activeUsers: ', activeUsers);
    }
  }, 5000);

  const changePos = (cr) => {
    let offset = [0, 0];
    let isDown = false;
    cr.addEventListener(
      "mousedown",
      function (e) {
        if (!cr.classList.contains("drop")) {
          cr.classList.add("drop");
        }
        console.log(cr.classList);

        isDown = true;
        offset = [cr.offsetLeft - e.clientX, cr.offsetTop - e.clientY];
      },
      true
    );
    document.addEventListener(
      "mouseup",
      function () {
        isDown = false;
      },
      true
    );

    document.addEventListener(
      "mousemove",
      function (e) {
        e.preventDefault();
        if (isDown) {
          cr.style.left = e.clientX + offset[0] + "px";
          cr.style.top = e.clientY + offset[1] + "px";
        }
      },
      true
    );
  };

  const chatBoxRemovePosition = () => {
    chatRef.current.removeAttribute("style");
    chatRefList.current.removeAttribute("style");
  };

  const chatBoxPosition = (cr) => {
    setTimeout(() => {
      cr.style.top = cr.getBoundingClientRect().top + "px";
      cr.style.left = cr.getBoundingClientRect().left + "px";
      cr.style.position = "fixed";
    }, 100);
  };

  function startVideoCall(id){
      console.log("user id: ", id);
      let user = userService.getUserInfo();
      user.uType = user.type;
      let noti = {
        code: 'user.video-call',
        actingUser: user,
        otherUser: {id: id}
      };
      messagingService.addNotification(noti).then(res => res.json())
                          .then(data => {
                            let fromId = user.id;
                            window.open("https://meeting.wteam.chat/?roomId=" + id + "&fromId=" + fromId + "&token=" + requests.getToken(), "_blank", "menubar=no,status=no,titlebar=no,toolbar=no,scrollbars=no,resizable=no,top=500,left=500,width=320,height=600");
                          })
                          .catch(err => {
                            console.log('members error: ', err);
     
                          });
      
  }

  function startChat(id){

      let fromId = userService.getUserInfo().id;
      window.open("https://chat.wteam.chat/?fromId=" + fromId + "&toId=" + id + "&token=" + requests.getToken(), "_blank", "menubar=no,status=no,titlebar=no,toolbar=no,scrollbars=no,resizable=no,top=500,left=500,width=320,height=450");
  }

  function isUserActive(id){
    return activeUsers.some(user => user.id == id);
  }

  function getUserUrl(id){
    return activeUsers.find(user => user.id == id) && activeUsers.find(user => user.id == id).tab ? true : false;
  }

  function goToDoc(id){
    let url = activeUsers.find(user => user.id == id).tab;
    window.open(url);
  }

  return (
    <div className={`teammates ${list ? "list" : ""}`} ref={ref}>
      <div className={`teammates-body ${list ? "list" : ""}`}>
        <div className="teammates-body__grid">
          <div className="teammates-about" onClick={(e) => setTools(!tools)}>
          {avatar &&
            <div className="teammates-about__image">
              <img src={avatar} alt="Person" />
            </div>
            }
            {!avatar &&
            <div className="teammates-about__image teammates-about__image--name">
              <p>{name.charAt(0)}{lastname.charAt(0)}</p>
            </div>
            }
            <div className="teammates-about__data">
              <div className="teammates-about__name">
                <h2>{name}</h2>

                

                {getUserUrl(id) &&
                  <a href="javascript:void(0)" onClick={() => {goToDoc(id)}}>
                    <img src={doc} alt="Doc" />
                  </a>
                }

              </div>
              <ul className="teammates-about__list">
                <li className="teammates-about__item">
                  <svg
                    className={`icon icon-circul ${
                      isUserActive(id) ? "online" : ""
                    }`}
                  >
                    <use href={icons + "#icon-circul"}></use>
                  </svg>
                  <span>
                    {isUserActive(id) ? "Active Now" : "Offline"}
                  </span>
                </li>
                <li className="teammates-about__item">
                  <svg className="icon icon-watch">
                    <use href={icons + "#icon-watch"}></use>
                  </svg>
                  <span>Time 2:30 PM</span>
                </li>
              </ul>
            </div>
          </div>
          <div
            className={`teammates-chat ${chatShow ? chatClass : ""}`}
            ref={chatRef}
          >
            <div className="teammates-chat__body">
              <div className="teammates-chat__content">
                <div className="teammates-chat__head">
                  <button className="teammates-chat__type">
                    <svg className="icon icon-video">
                      <use href={icons + "#icon-video"}></use>
                    </svg>
                  </button>
                  <button className="teammates-chat__type">
                    <svg className="icon icon-talk">
                      <use href={icons + "#icon-talk"}></use>
                    </svg>
                  </button>
                  <button
                    className="teammates-chat__type close"
                    onClick={() => {
                      setChatShow(false);
                      chatBoxRemovePosition();
                    }}
                  >
                    <svg className="icon icon-close">
                      <use href={icons + "#icon-close"}></use>
                    </svg>
                  </button>
                </div>
                <div className="teammates-chat__messages">
                  <div className="teammates-chat__text right">
                    <span>Hi</span>
                  </div>
                  <div className="teammates-chat__text right">
                    <span>How are you?</span>
                  </div>
                  <div className="teammates-chat__text left">
                    <span>Hello</span>
                  </div>
                  <div className="teammates-chat__text left">
                    <span>i'm fine</span>
                  </div>
                </div>
                <div className="teammates-tools__write teammates-chat__write">
                  <input
                    type="text"
                    placeholder="Type something which make me happy"
                  />
                </div>
              </div>
            </div>
          </div>

          {tools ? (
            <div className="teammates-body__content">
              <ul className="teammates-tools">
                <li className="teammates-tools__item">
                  <svg className="icon icon-talk">
                    <use href={icons + "#icon-talk"}></use>
                  </svg>
                  <span>Talk</span>
                </li>
                <li className="teammates-tools__item" onClick={(e) => startVideoCall(id)}>
                  <svg className="icon icon-video">
                    <use href={icons + "#icon-video"}></use>
                  </svg>
                  <span>Video</span>
                </li>

                {getUserUrl(id) &&
                  <li className="teammates-tools__item">
                  <img src={doc} alt="Doc" />
                  <span>
                    Join ---{" "}
                    <a href="javascript:void(0)" onClick={() => {goToDoc(id)}}>
                      Presentaion documents https://docs.google…
                    </a>
                  </span>
                </li>
                }
                

                <li
                  className="teammates-tools__item"
                  onClick={(e) => {
                    startChat(id);
                  }}
                >
                  <svg className="icon icon-messageFull">
                    <use href={icons + "#icon-messageFull"}></use>
                  </svg>
                  <span>View Message</span>
                </li>
              </ul>
              <div className="teammates-tools__write">
                <input type="text" placeholder="Write message..." />
              </div>
            </div>
          ) : null}
        </div>
        <div className="teammates-body__list">
          <div className="teammates-list">
            <div className="teammates-list__image">
              <img src={person} alt="Person" />
            </div>
            <div className="teammates-list__name">
              <span>{name}</span>
            </div>
            <div className="teammates-list__status">
              <svg
                className={`icon icon-circul ${
                  isUserActive(id) ? "online" : ""
                }`}
              >
                <use href={icons + "#icon-circul"}></use>
              </svg>
              <span>
                {isUserActive(id) ? "Active Now" : "Offline"}
              </span>
            </div>
            <div className="teammates-list__time">
              <svg className="icon icon-watch">
                <use href={icons + "#icon-watch"}></use>
              </svg>
              <span>2:30 PM</span>
            </div>

            {getUserUrl(id) &&
                <div className="teammates-list__document">
                  <a href="javascript:void(0)" onClick={() => {goToDoc(id)}}>
                    <img src={doc} alt="Doc" />
                    <span>Google Docs</span>
                  </a>
                </div>
                }

            
            <button className="teammates-list__button">
              <svg className="icon icon-talk">
                <use href={icons + "#icon-talk"}></use>
              </svg>
            </button>
            <button className="teammates-list__button">
              <svg className="icon icon-video">
                <use href={icons + "#icon-video"}></use>
              </svg>
            </button>
            <button
              className={`teammates-list__button ${chatShow ? "chat" : ""}`}
              onClick={() => {
                setChatShow(!chatShow);
                chatBoxPosition(chatRefList.current);
              }}
            >
              <svg className="icon icon-messageFull">
                <use href={icons + "#icon-messageFull"}></use>
              </svg>
            </button>
          </div>
          <div
            className={`teammates-chat  ${chatShow ? "open" : ""}`}
            ref={chatRefList}
          >
            <div className="teammates-chat__body">
              <div className="teammates-chat__content">
                <div className="teammates-chat__head">
                  <button className="teammates-chat__type">
                    <svg className="icon icon-video">
                      <use href={icons + "#icon-video"}></use>
                    </svg>
                  </button>
                  <button className="teammates-chat__type">
                    <svg className="icon icon-talk">
                      <use href={icons + "#icon-talk"}></use>
                    </svg>
                  </button>
                  <button
                    className="teammates-chat__type close"
                    onClick={() => {
                      setChatShow(false);
                      chatBoxRemovePosition();
                    }}
                  >
                    <svg className="icon icon-close">
                      <use href={icons + "#icon-close"}></use>
                    </svg>
                  </button>
                </div>
                <div className="teammates-chat__messages">
                  <div className="teammates-chat__text right">
                    <span>Hi</span>
                  </div>
                  <div className="teammates-chat__text right">
                    <span>How are you?</span>
                  </div>
                  <div className="teammates-chat__text left">
                    <span>Hello</span>
                  </div>
                  <div className="teammates-chat__text left">
                    <span>i'm fine</span>
                  </div>
                </div>
                <div className="teammates-tools__write teammates-chat__write">
                  <input
                    type="text"
                    placeholder="Type something which make me happy"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
