import React, { useState } from "react";
import icons from "../../../assets/image/icons.svg";

function Teammember({ person, name, admin }) {
  const [open, setOpen] = useState(false);
  return (
    <div className="team-member">
      <div className="team-member__about">
        <img className="image" src={person} alt="Person" />
        <span className="name">{name}</span>
        {admin && <span className="admin">admin</span>}
        <div className="team-tools" onClick={() => setOpen(!open)}>
          <div className="team-tools__button">
            <svg className="icon icon-dost">
              <use href={icons + "#icon-dost"}></use>
            </svg>
          </div>
          {open && (
            <div className="team-tools__content">
              <div className="team-tools__list">
                <div
                  className="team-tools__item"
                  onClick={() => setOpen(!open)}
                >
                  <svg className="icon icon-person">
                    <use href={icons + "#icon-person"}></use>
                  </svg>
                  <span>Make admin</span>
                </div>
                <div
                  className="team-tools__item"
                  onClick={() => setOpen(!open)}
                >
                  <svg className="icon icon-delete">
                    <use href={icons + "#icon-delete"}></use>
                  </svg>
                  <span>Remove from team</span>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Teammember;
