import React, { useState, useEffect } from "react";
import DatePicker from "react-datepicker";
import icons from "../../../assets/image/icons.svg";
import { EmojiButton } from "@joeattardi/emoji-button";

function StatusWrite() {
  const [emojiChoose, setEmojiChoose] = useState(null);
  useEffect(() => {
    const picker = new EmojiButton({
      position: {
        top: `-250px`,
        left: `2px`,
      },
      strategy: "absolute",
      showVariants: true,
      emojiSize: "16px",
      showPreview: false,
    });
    const trigger = document.querySelector("#emoji-trigger");

    picker.on("emoji", (selection) => {
      setEmojiChoose(selection.emoji);
    });

    trigger.addEventListener("click", () => picker.togglePicker(trigger));

    const emoji = document.querySelector(".emoji-picker__wrapper");
    const emojiContent = document.querySelector("#emoji");
    emojiContent.appendChild(emoji);
  }, []);

  const [startDate, setStartDate] = useState(new Date());
  const [write, setWrite] = useState(false);

  return (
    <div className="status-write">
      <div className="status-write__body">
        <div id="emoji"></div>
        <button id="emoji-trigger" className="status-write__emoji">
          {emojiChoose ? (
            emojiChoose
          ) : (
            <svg className="icon icon-emoji">
              <use href={icons + "#icon-emoji"}></use>
            </svg>
          )}
        </button>

        <input
          className="status-write__text"
          type="text"
          placeholder="Write a status…"
          onFocus={() => setWrite(true)}
        />
      </div>
      {write ? (
        <div className="status-write__tools">
          <div className="status-edit status-write__edit">
            <DatePicker
              selected={startDate}
              onChange={(date) => setStartDate(date)}
              showTimeSelect
              showTimeSelectOnly
              timeIntervals={15}
              timeCaption="Time"
              dateFormat="h:mm aa"
            />
            {/* <input type="text" placeholder="Time" /> */}
            <button onClick={() => setWrite(false)}>Save</button>
          </div>
        </div>
      ) : null}
    </div>
  );
}

export default StatusWrite;
