import React, { useState, useRef, useEffect } from "react";
import DatePicker from "react-datepicker";
import icons from "../../../assets/image/icons.svg";

function Statusitem({ icon, name, text }) {
  const [startDate, setStartDate] = useState(new Date());
  const [edit, setEdit] = useState(false);
  const ref = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setEdit(false);
      }
    }
    document.addEventListener("mouseup", handleClickOutside);
    return () => {
      document.removeEventListener("mouseup", handleClickOutside);
    };
  }, [ref]);
  return (
    <li className="status-item" ref={ref}>
      <div className="status-item__content">
        <div
          className={`status-item__about ${!edit && "close"}`}
          onClick={() => setEdit(true)}
        >
          {/* <img className="status-item__icon" src={icon} alt="Focus" /> */}
          <span>{icon}</span>
          <span className={`status-item__name ${edit ? "bg-color" : ""}`}>
            {name}
          </span>
          {!edit ? (
            <span className="status-item__delete">
              <svg className="icon icon-cross">
                <use href={icons + "#icon-cross"}></use>
              </svg>
            </span>
          ) : null}
        </div>
        {edit ? (
          <div className="status-edit">
            <DatePicker
              selected={startDate}
              onChange={(date) => setStartDate(date)}
              showTimeSelect
              showTimeSelectOnly
              timeIntervals={15}
              timeCaption="Time"
              dateFormat="h:mm aa"
            />
            {/* <input type="text" placeholder="Time" /> */}
            <button onClick={() => setEdit(false)}>Save</button>
          </div>
        ) : null}
      </div>
      {edit ? <span className="status-item__text">{text}</span> : null}
    </li>
  );
}

export default Statusitem;
