import React from "react";
import StLayout from "../StLayout/StLayout";
import "../Settings.css";

import SharingTeammate from "../Sharing/SharingTeammate";

function Integration({ logo, name, text, aboutImage, teammates }) {
  return (
    <StLayout page="">
      <div className="integration">
        <div className="integration-body">
          <div className="integration-body__head">
            <h3>Integrate your apps with wteam</h3>
          </div>
          <div className="integration-app">
            <div className="integration-app__head">
              <div className="integration-app__about">
                <img src={logo} alt="Google" />
                <h3>{name}</h3>
              </div>
              <button className="integration-app__connect">connect</button>
            </div>
            <div className="sharing-content integration-content">
              <div className="sharing-content__head">
                <h2>Instraction</h2>
                {text.map((t, i) => (
                  <p key={i}>{t}</p>
                ))}
              </div>
              <div className="sharing-bottom">
                <div className="sharing-bottom__content">
                  {teammates ? (
                    <SharingTeammate
                      name={teammates.name}
                      meeting={teammates.meeting}
                    />
                  ) : null}
                </div>
                <div className="sharing-content__image">
                  {aboutImage ? <img src={aboutImage} alt={name} /> : null}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </StLayout>
  );
}

export default Integration;
