import React, { useContext, useRef, useEffect } from "react";
import { useHistory } from "react-router-dom";
import Dropdowns from "../../components/UI/Dropdown/Dropdown";
import Invite from "../../components/UI/Invite/Invite";
import icons from "../../assets/image/icons.svg";
import { SearchTheme } from "../../context/SearchContext";
import { TableTheme } from "../../context/TableVariant";
import "./Content.css";

function Content(props) {
  const { search, changeSearch } = useContext(SearchTheme);
  const { variant, changeVariant } = useContext(TableTheme);
  const { showTable, showNotificationDelete, page, showInviteButton } = props;
  const history = useHistory();
  const prevPath = usePrevious(history.location.pathname);

  useEffect(() => {
    if (history.location.pathname !== prevPath) {
      changeSearch("");
    }
  }, [history.location.pathname, prevPath, changeSearch]);

  function usePrevious(value) {
    const ref = useRef();
    useEffect(() => {
      ref.current = value;
    });
    return ref.current;
  }

  return (
    <section className="page">
      <div className="page-body">
        <div className="page-sticky">
          <div className="page-head">
            <div className="page-content">
              <h1 className="page-content__title">{page}</h1>
              <div className="page-content__search">
                <input
                  id="searchInput"
                  type="text"
                  placeholder="Search"
                  value={search}
                  onChange={(e) => changeSearch(e.target.value)}
                />
                <button>
                  <svg className="icon icon-search">
                    <use href={icons + "#icon-search"}></use>
                  </svg>
                </button>
              </div>
            </div>
          </div>
          <div className="page-tools">
            <div className="page-tools__left">
              <div className="page-filter">
                <div className="page-filter__body">
                  <div className="page-filter__select">
                    <Dropdowns values={["Active"]} />
                  </div>
                  <div className="page-filter__select">
                    <Dropdowns values={["Local time"]} />
                  </div>
                  <div className="page-filter__select">
                    <Dropdowns values={["App "]} />
                  </div>
                  {showInviteButton ? null : (
                    <div className="page-filter__invite page-filter__select">
                      <Invite />
                    </div>
                  )}
                </div>
              </div>
            </div>
            <div className="page-tools__right">
              {showTable ? (
                <div className="page-choose">
                  <ul className="page-choose__list">
                    <li
                      className={`page-choose__item ${
                        variant === "apps" ? "active" : ""
                      }`}
                      onClick={() => changeVariant("apps")}
                    >
                      <svg className="icon icon-apps">
                        <use href={icons + "#icon-apps"}></use>
                      </svg>
                    </li>
                    <li
                      className={`page-choose__item ${
                        variant === "list" ? "active" : ""
                      }`}
                      onClick={() => changeVariant("list")}
                    >
                      <svg className="icon icon-list">
                        <use href={icons + "#icon-list"}></use>
                      </svg>
                    </li>
                  </ul>
                </div>
              ) : null}
              {showNotificationDelete ? (
                <button className="page-tools__delete"> Delete All</button>
              ) : null}
            </div>
          </div>
        </div>
        <div className="page-inner">{props.children}</div>
      </div>
    </section>
  );
}

export default Content;
