import React, { useState, useEffect, useRef } from "react";
import "./Notifications.css";
import icons from "../../../assets/image/icons.svg";
import person from "../../../assets/image/other/person.png";
import { Link } from "react-router-dom";

function Notifications() {
  const [open, setOpen] = useState(false);

  const ref = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setOpen(false);
      }
    }
    document.addEventListener("click", handleClickOutside);
    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, [ref]);

  return (
    <div className="notifications" ref={ref}>
      <div className="notifications-body">
        <div className="notifications-toggle" onClick={() => setOpen(!open)}>
          <svg className="icon icon-notifications">
            <use href={icons + "#icon-notifications"}></use>
          </svg>
        </div>
        <div className={`notifications-content ${open ? "open" : ""}`}>
          <div className="notifications-content__body">
            <ul className="notifications-list">
              <li className="notifications-item">
                <div className="notifications-item__person">
                  <img src={person} alt="Person" />
                  <span>Orkhan</span>
                </div>
                <div className="notifications-item__about">
                  <svg className="icon icon-call-missed">
                    <use href={icons + "#icon-call-missed"}></use>
                  </svg>
                  <span>Missed Video Call</span>
                </div>
                <div className="notifications-item__time">
                  <span>12:20 am</span>
                </div>
              </li>
              <li className="notifications-item">
                <div className="notifications-item__person">
                  <img src={person} alt="Person" />
                  <span>Jena</span>
                </div>
                <div className="notifications-item__about">
                  <svg className="icon icon-call-missed">
                    <use href={icons + "#icon-call-missed"}></use>
                  </svg>
                  <span>Missed Video Call</span>
                </div>
                <div className="notifications-item__time">
                  <span>14:10 am</span>
                </div>
              </li>
              <li className="notifications-item">
                <div className="notifications-item__person">
                  <img src={person} alt="Person" />
                  <span>Jack</span>
                </div>
                <div className="notifications-item__about">
                  <svg className="icon icon-message-square">
                    <use href={icons + "#icon-message-square"}></use>
                  </svg>
                  <span>Missed Messagel</span>
                </div>
                <div className="notifications-item__time">
                  <span>14:10 am</span>
                </div>
              </li>
            </ul>
          </div>
          <div className="notifications-content__more">
            <Link to="/notifications">see all</Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Notifications;
