import React, { useEffect, useContext } from "react";
import Layout from "../../hoc/Layout/Layout";
import Content from "../../hoc/Content/Content";
import "./Notifications.css";
import { TableTheme } from "../../context/TableVariant";
import { SearchTheme } from "../../context/SearchContext";
import NotificationItem from "./NotificationItem";

function Notifications(props) {
  let notificationArr = [
    { name: "Orxan", meeting: false, online: false },
    { name: "Tural", meeting: true, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Tural", meeting: true, online: true },
    { name: "Orxan", meeting: false, online: false },
    { name: "Tural", meeting: true, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Tural", meeting: true, online: true },
    { name: "Orxan", meeting: false, online: false },
    { name: "Tural", meeting: true, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Tural", meeting: true, online: true },
    { name: "Orxan", meeting: false, online: false },
    { name: "Tural", meeting: true, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Tural", meeting: true, online: true },
    { name: "Orxan", meeting: false, online: false },
    { name: "Tural", meeting: true, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Tural", meeting: true, online: true },
    { name: "Orxan", meeting: false, online: false },
    { name: "Tural", meeting: true, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Valeh", meeting: false, online: true },
    { name: "Tural", meeting: true, online: true },
  ];
  const { changeVariant } = useContext(TableTheme);
  const { search } = useContext(SearchTheme);

  const results = notificationArr.filter((team) => {
    return (
      team.name.toLocaleLowerCase().indexOf(search.toLocaleLowerCase()) !== -1
    );
  });

  useEffect(() => {
    changeVariant("apps");
  }, [changeVariant]);

  return (
    <Layout>
      <Content
        page="Notifications"
        showTable={false}
        showNotificationDelete
        showInviteButton
      >
        <div className="notification">
          <div className="notification-body">
            <div className="notification-list">
              {results.map((n, i) => (
                <NotificationItem
                  key={i}
                  name={n.name}
                  meeting={n.meeting}
                  onlie={n.online}
                />
              ))}
            </div>
          </div>
        </div>
      </Content>
    </Layout>
  );
}

export default Notifications;
